.container {
  margin: 0 auto;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;

  &--none {
    padding-left: 0;
    padding-right: 0;
  }
}

@media (min-width: $desktop-width) {
  .container {
    padding-left: 130px;
    padding-right: 130px;
    max-width: $desktop-width;
  }
}
